.sidebar {
    position: fixed;
    width: 300px;
    left: 20px;
    height: calc(100vh - 108px);
    margin-bottom: 20px;
    background-color: $color_secondary;
    background-image: linear-gradient($color_secondary, $color_secondary), linear-gradient( 180deg, rgba(255, 255, 255, 0.19) 0%, rgba(255, 255, 255, 0) 100%);
    background-origin: border-box;
    background-clip: content-box, border-box;
    border: none;
    border-radius: $border_radius;
    z-index: 1;
    @include xl {
        width: 100px;
    }
    .price {
        padding: 30px 20px;
        background-color: #354DD4;
        background-image: url(../img/deco.svg);
        background-size: cover;
        border-radius: 20px;
        margin: 0 0 30px;
        p {
            color: rgba(255, 255, 255, 0.664);
        }
        img {
            width: 100%
        }
        h2 {
            text-align: center !important;
            margin: auto
        }
        .btn {
            background-color: #1f3199;
            i {
                color: $color_yellow
            }
        }
    }
    .user {
        align-items: center;
        .user_img {
            height: 70px;
            width: 70px;
            border-radius: 50%;
            margin-right: 10px;
        }
        .title {
            font-size: 17px;
        }
    }
    @include md {
        height: auto;
        position: relative;
        width: auto;
        left: auto;
        margin-left: 15px;
        margin-right: 15px;
    }
    .sidabar_content {
        height: 100%;
        padding: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        @include xl {
            width: 100px;
            padding: 16px;
        }
        @include md {
            flex-direction: row;
            width: 100%;
        }
        .categories {
            overflow: hidden;
            @include md {
                overflow: hidden;
                overflow-x: scroll;
                display: flex;
                justify-content: center;
            }
            .marketplace_pages {
                margin-bottom: 30px;
                @include xl {
                    margin-bottom: 0;
                }
            }
            .marketplace_pages,
            .account_pages {
                .pages_title {
                    color: $color_brand;
                    font-weight: normal;
                    margin-bottom: 15px;
                    font-size: 14px;
                    letter-spacing: 2px;
                }
                .pages_title {
                    @include xl {
                        display: none;
                    }
                }
                .list_items {
                    @include md {
                        display: flex;
                    }
                    li {
                        margin-bottom: 5px;
                        &:last-of-type {
                            margin-bottom: 0;
                        }
                        a {
                            display: flex;
                            align-items: center;
                            color: $color_text;
                            padding: 5px 0;
                            border-radius: 12px;
                            transition: all 0.25s $transition_function;
                            position: relative;
                            p {
                                transition: all 0.25s $transition_function;
                            }
                            .number {
                                position: absolute;
                                right: 0;
                            }
                            &:hover {
                                color: $color_brand;
                                p {
                                    color: white;
                                }
                            }
                            i {
                                font-size: 24px;
                                margin-right: 10px;
                                @include xl {
                                    margin-right: 0;
                                }
                            }
                            &.active {
                                color: $color_brand;
                                p {
                                    color: $color_white;
                                }
                                &:hover {
                                    color: $color_white;
                                    p {
                                        color: $color_white;
                                    }
                                }
                            }
                            .links_name {
                                font-size: 16px;
                                @include xl {
                                    display: none;
                                    white-space: nowrap;
                                    opacity: 0;
                                    pointer-events: none;
                                }
                            }
                            .tooltip {
                                position: absolute;
                                top: -20px;
                                left: calc(100% + 15px);
                                z-index: 3;
                                background: #fff;
                                box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
                                padding: 6px 12px;
                                border-radius: 4px;
                                font-size: 15px;
                                font-weight: 400;
                                opacity: 0;
                                white-space: nowrap;
                                pointer-events: none;
                                transition: 0s;
                            }
                            @include xl {
                                display: flex;
                                justify-content: center;
                            }
                        }
                    }
                }
            }
        }
        .logout {
            margin-top: 40px;
            @include md {
                display: block;
                margin-top: 0;
                margin-left: 40px;
            }
            a {
                display: flex;
                align-items: center;
                color: $color_text;
                transition: all 0.25s $transition_function;
                i {
                    font-size: 24px;
                    margin-right: 5px;
                }
                p {
                    transition: all 0.25s $transition_function;
                }
                &:hover {
                    color: $color_red;
                    p {
                        color: $color_red;
                    }
                }
            }
            @include xl {
                display: flex;
                justify-content: center;
                p {
                    display: none;
                }
            }
        }
    }
}